
body {
  margin: 0em;
  padding: 0em;
}
.nav {
  position: fixed !important;
  z-index: 999;
  top: 0;
  left: 0;
  right: 0;
  margin: 1em 0em 0em 1em;
  font-family: "soleil", sans-serif;
  width: 10%;
}
@media only screen and (max-width: 768px) {
.nav {
      margin: 0em;
      background: #fff;
      padding-left: 1.5em;
      width: 100%;
      -webkit-box-shadow: 0px 10px 32px -8px rgba(0, 0, 0, 0.25);
      -moz-box-shadow: 0px 10px 32px -8px rgba(0, 0, 0, 0.25);
      box-shadow: 0px 10px 32px -8px rgba(0, 0, 0, 0.25);
}
}
.nav a {
    text-decoration: none;
    font-size: 1.25em;
    color: #e9ac00;
    display: block;
    margin-bottom: 1em;
    width: fit-content;
}
@media only screen and (max-width: 768px) {
.nav a {
        display: inline-block;
        margin: .5em 1em .5em 0em;
}
}
.nav a:visited {
      color: #e9ac00;
}
.nav a:hover {
      color: #577b90;
}
.page {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 3em;
}
@media only screen and (max-width: 768px) {
.page {
      margin-top: 4em;
}
}
.page .page-body {
    width: 60%;
    text-align: left;
    margin-bottom: 5em;
}
.page .page-body.small {
      width: 45%;
}
@media only screen and (max-width: 768px) {
.page .page-body {
        width: 100%;
}
.page .page-body.small {
          width: 90%;
}
}
.page .page-body ul {
      list-style: none;
      padding-left: 0em;
}
.page .page-body a {
      text-decoration: none;
}
.page .page-body a:visited {
        text-decoration: none;
}
.content h2 {
  font-family: "soleil", sans-serif;
  font-weight: 900;
  font-size: 2.5rem;
  color: #e9ac00;
  margin: .4em 0em .1em 0em;
}
@media only screen and (max-width: 768px) {
.content h2 {
      font-size: 2.2rem;
}
}
.content h3 {
  font-family: "soleil", sans-serif;
  font-weight: 900;
  font-size: 1.5rem;
  color: #000;
  margin: .2em 0em;
}
.content h4 {
  font-family: "soleil", sans-serif;
  font-size: 1.5rem;
  font-weight: 400;
  color: #999;
  margin: 0em 0em .6em 0em;
}
@media only screen and (max-width: 768px) {
.content h4 {
      font-size: 1.25rem;
}
}
.content h5 {
  font-family: "soleil", sans-serif;
  font-size: 1.35rem;
  margin: 1.2em 0em .6em 0em;
}
@media only screen and (max-width: 768px) {
.content h5 {
      font-size: 1.1rem;
}
}
.content p {
  white-space: pre-wrap;
  font-family: "adobe-caslon-pro", serif;
  font-size: 1.25rem;
  margin: .2em 0em;
}
@media only screen and (max-width: 768px) {
.content p {
      font-size: 1rem;
}
}
h1, h2, h3, h4 {
  font-family: "soleil", sans-serif;
  margin: 0em;
}
.information {
  margin-bottom: 2em;
}
.information h1 {
    color: #e9ac00;
    font-size: 3rem;
}
@media only screen and (max-width: 768px) {
.information h1 {
        font-size: 2.5rem;
}
}
.information h2 {
    color: #577b90;
    margin: 0em;
}
@media only screen and (max-width: 768px) {
.information h2 {
        font-size: 1.25rem;
}
}
.information p {
    font-family: "adobe-caslon-pro", serif;
    font-size: 1rem;
}
.item {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 50%;
  height: 12rem;
  box-sizing: border-box;
  font-size: .85rem;
}
@media only screen and (max-width: 768px) {
.item {
      width: 100%;
}
}
.item.kent {
    background-color: #222;
    color: #fff;
    transition: transform .2s ease-in;
    z-index: 10;
}
.item.kent:hover {
      background-color: #272727;
      transform: scale(1.05, 1.05);
      z-index: 1000;
      box-shadow: 0px 5px 20px rgba(0, 0, 0, 0.4);
}
.item.deco {
    background-color: #fafafa;
    color: #333;
    transition: transform .2s ease-in;
    z-index: 10;
}
.item.deco:hover {
      background-color: white;
      transform: scale(1.05, 1.05);
      z-index: 1000;
      box-shadow: 0px 5px 20px rgba(0, 0, 0, 0.4);
}
.item.deco:visited {
      color: #333;
}
.item.rapidex {
    background-color: #ee6767;
    color: #fff;
    transition: transform .2s ease-in;
    z-index: 10;
}
.item.rapidex:hover {
      background-color: #ef7070;
      transform: scale(1.05, 1.05);
      z-index: 1000;
      box-shadow: 0px 5px 20px rgba(0, 0, 0, 0.4);
}
.item.clicker {
    background: linear-gradient(to bottom right, #2EC4B6, #20A4F3, #973FDB);
    background: -webkit-linear-gradient(left top, #2EC4B6, #20A4F3, #973FDB);
    transition: transform .2s ease-in;
    color: #fff;
}
.item.clicker:hover {
      transform: scale(1.05, 1.05);
      z-index: 1000;
      box-shadow: 0px 5px 20px rgba(0, 0, 0, 0.4);
}
.item.cheater {
    background-color: #e9ac00;
    color: #fff;
    transition: transform .2s ease-in;
    z-index: 10;
}
.item.cheater:hover {
      background-color: #f3b400;
      transform: scale(1.05, 1.05);
      z-index: 1000;
      box-shadow: 0px 5px 20px rgba(0, 0, 0, 0.4);
}
.item.gaming {
    background-color: #2de5c6;
    color: #fff;
    transition: transform .2s ease-in;
    z-index: 10;
}
.item.gaming:hover {
      background-color: #36e6c8;
      transform: scale(1.05, 1.05);
      z-index: 1000;
      box-shadow: 0px 5px 20px rgba(0, 0, 0, 0.4);
}



