
body {
  margin: 0em;
  padding: 0em;
}
.nav {
  position: fixed !important;
  z-index: 999;
  top: 0;
  left: 0;
  right: 0;
  margin: 1em 0em 0em 1em;
  font-family: "soleil", sans-serif;
  width: 10%;
}
@media only screen and (max-width: 768px) {
.nav {
      margin: 0em;
      background: #fff;
      padding-left: 1.5em;
      width: 100%;
      -webkit-box-shadow: 0px 10px 32px -8px rgba(0, 0, 0, 0.25);
      -moz-box-shadow: 0px 10px 32px -8px rgba(0, 0, 0, 0.25);
      box-shadow: 0px 10px 32px -8px rgba(0, 0, 0, 0.25);
}
}
.nav a {
    text-decoration: none;
    font-size: 1.25em;
    color: #e9ac00;
    display: block;
    margin-bottom: 1em;
    width: fit-content;
}
@media only screen and (max-width: 768px) {
.nav a {
        display: inline-block;
        margin: .5em 1em .5em 0em;
}
}
.nav a:visited {
      color: #e9ac00;
}
.nav a:hover {
      color: #577b90;
}
.page {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 3em;
}
@media only screen and (max-width: 768px) {
.page {
      margin-top: 4em;
}
}
.page .page-body {
    width: 60%;
    text-align: left;
    margin-bottom: 5em;
}
.page .page-body.small {
      width: 45%;
}
@media only screen and (max-width: 768px) {
.page .page-body {
        width: 100%;
}
.page .page-body.small {
          width: 90%;
}
}
.page .page-body ul {
      list-style: none;
      padding-left: 0em;
}
.page .page-body a {
      text-decoration: none;
}
.page .page-body a:visited {
        text-decoration: none;
}
.content h2 {
  font-family: "soleil", sans-serif;
  font-weight: 900;
  font-size: 2.5rem;
  color: #e9ac00;
  margin: .4em 0em .1em 0em;
}
@media only screen and (max-width: 768px) {
.content h2 {
      font-size: 2.2rem;
}
}
.content h3 {
  font-family: "soleil", sans-serif;
  font-weight: 900;
  font-size: 1.5rem;
  color: #000;
  margin: .2em 0em;
}
.content h4 {
  font-family: "soleil", sans-serif;
  font-size: 1.5rem;
  font-weight: 400;
  color: #999;
  margin: 0em 0em .6em 0em;
}
@media only screen and (max-width: 768px) {
.content h4 {
      font-size: 1.25rem;
}
}
.content h5 {
  font-family: "soleil", sans-serif;
  font-size: 1.35rem;
  margin: 1.2em 0em .6em 0em;
}
@media only screen and (max-width: 768px) {
.content h5 {
      font-size: 1.1rem;
}
}
.content p {
  white-space: pre-wrap;
  font-family: "adobe-caslon-pro", serif;
  font-size: 1.25rem;
  margin: .2em 0em;
}
@media only screen and (max-width: 768px) {
.content p {
      font-size: 1rem;
}
}
.resume-download {
  text-align: center;
  margin-top: 3em;
}
.resume-download a {
    font-family: sans-serif;
    color: #577b90;
}
.resume-holder {
  width: 50%;
  margin: .25em auto;
  border: 8px solid #222;
}
@media only screen and (max-width: 768px) {
.resume-holder {
      width: 90%;
}
}



