
.simple {
  background-color: #E8E8E8;
  color: #2b2b2b;
}
.special {
  background-color: #74C773;
  color: #fff;
}
.extraordinary {
  background-color: #3DA4CF;
  color: #fff;
}
.legendary {
  background-color: #8C8CEA;
  color: #fff;
}
.mythic {
  background-color: #85DDC9;
  color: #fff;
}
.familiar {
  background-color: #85877C;
  color: #fff;
}
body {
  background: #eee;
  font-family: sans-serif;
}
.button, .close-button, .sort-container .rarity-button, .search-container .search-button, .pack-container .pack-button {
  border: none;
}
.button:hover, .close-button:hover, .sort-container .rarity-button:hover, .search-container .search-button:hover, .pack-container .pack-button:hover {
    cursor: pointer;
}
.button:focus, .close-button:focus, .sort-container .rarity-button:focus, .search-container .search-button:focus, .pack-container .pack-button:focus {
    outline: none;
}
.button-container {
  height: 3em;
  width: 100%;
}
.close-button {
  float: right;
  padding: .75em;
  margin-right: 1em;
}
.sort-container {
  display: inline-block;
  margin-left: .75em;
  margin-bottom: .75em;
}
@media screen and (max-width: 720px) {
.sort-container {
      width: 100%;
      padding: 0em 1em;
      margin-left: 0em;
      box-sizing: border-box;
}
}
.sort-container .rarity-button {
    padding: .5em;
}
.sort-container h3 {
    margin: 1em 0em;
}
.search-container {
  display: inline-block;
  margin-left: 2.5em;
}
@media screen and (max-width: 720px) {
.search-container {
      width: 100%;
      margin-left: 0em;
      padding: 0em 1em;
      box-sizing: border-box;
}
}
.search-container input {
    padding: .75em;
    height: 3em;
    box-sizing: border-box;
    border: none;
    border-radius: 4px;
    width: 20em;
}
@media screen and (max-width: 720px) {
.search-container input {
        width: 100%;
}
}
.search-container input:focus {
      outline: none;
}
.search-container .search-button {
    box-sizing: border-box;
    height: 3em;
    border-radius: 0px 4px 4px 0px;
}
.pack-container {
  display: inline-block;
  float: right;
  margin-right: 2em;
  margin-top: 1em;
  vertical-align: top;
}
@media screen and (max-width: 720px) {
.pack-container {
      float: none;
      padding: 0em 1em;
      box-sizing: border-box;
      width: 100%;
}
}
.pack-container .pack-button {
    background-color: #D3B326;
    color: #fff;
    font-weight: 500;
    padding: 2em 1em;
}
@media screen and (max-width: 720px) {
.pack-container .pack-button {
        width: 100%;
}
}
.modal-container {
  z-index: 99990;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  left: 0px;
  top: 0px;
}
.pack-modal {
  z-index: 99999;
  background-color: #eee;
  padding: 1em;
  box-sizing: border-box;
  height: 28em;
  margin: 2em 0em;
  border-radius: 4px;
}
@media screen and (max-width: 720px) {
.pack-modal {
      margin: 1em;
      overflow-x: hidden;
      height: 100%;
}
}
.pack-open-enter-active, .pack-open-leave-active {
  transition: all 1s;
}
.pack-open-enter, .pack-open-leave-to {
  opacity: 0;
  transform: translateY(30px);
}



